<template>
  <div class="ss">
    <div v-if="show">
      <div class="input">
        <input
          type="text"
          placeholder="搜索"
          ref="getFocus"
          v-model="value"
          @keyup.enter="addup(value), input(value)"
        />
        <span @click="fh()">取消</span>
      </div>
      <div class="lsss">
        <div class="bar">
          <p>搜索历史</p>
          <img
            class="btn-delete"
            src="https://pic.qtfm.cn/qt-msite/icon-delete@3x.png"
            alt="btn"
            @click="empty"
          />
        </div>
        <div class="content">
          <span
            v-for="(histor, i) in history"
            :key="i"
            @click="addup(histor), input(histor)"
            >{{ histor }}</span
          >
        </div>
      </div>
      <div class="rmss">
        <p>热门搜索</p>
        <div class="content">
          <HotSs
            v-for="(hse, index) in this.hs.slice(0, 10)"
            :key="index"
            :hse="hse"
            :index="index"
            @input="addup($event), input($event)"
          />
        </div>
      </div>
      <a class="dbtn">
        <div class="text">查看更多</div>
        <img
          class="icon"
          src="https://pic.qtfm.cn/qt-msite/icon-arrow-right-red@3x.png"
          alt="icon"
        />
      </a>
    </div>
    <SsInput
      v-else
      :inputz="inputz"
      :value="value"
      :loadings="loadings"
      @input="addup($event), input($event)"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";
import HotSs from "@/components/HotSs.vue";
import SsInput from "@/components/SsInput.vue";

export default {
  data: function () {
    return {
      value: "",
      history: JSON.parse(localStorage.getItem("history")) || [],
      show: true,
      inputz: [],
      hs: [],
      loadings: false,
    };
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/ximalaya/search/words")
      .then((res) => {
        this.hs = res.data.data;
      });
  },
  mounted() {
    this.$refs.getFocus.focus();
  },
  methods: {
    empty() {
      this.history = [];
    },
    fh() {
      this.$router.push({ path: "/" });
    },

    input(value) {
      this.value = value;
      console.log("正在搜索");
      console.log(value);
      this.loadings = true;
      this.axios
        .get("https://apis.netstart.cn/ximalaya/search", {
          params: { kw: value, core: "all", page: 1, rows: 20 },
        })
        .then((res) => {
          console.log(res.data.data);
          this.inputz = res.data.data;
          this.loadings = false;
        });
    },
    addup(value) {
      this.history = [...new Set([value, ...this.history])];
      this.show = false;
    },
  },
  computed: {
    ...mapState(["values"]),
  },
  watch: {
    history: function (newHistory) {
      localStorage.setItem("history", JSON.stringify(newHistory));
    },
    getSuggests: function (newHistory) {
      localStorage.setItem("history", JSON.stringify(newHistory));
    },
  },
  components: { HotSs, SsInput },
};
</script>

<style lang="less" scoped>
.ss {
  .input {
    display: flex;
    align-items: center;
    width: 100%;
    height: 50rem;
    margin-bottom: -8rem;
    padding: 0 16rem;
    background-color: rgba(255, 255, 255, 1);

    input {
      flex-grow: 1;
      width: 0;
      height: 32rem;
      margin-right: 16rem;
      padding: 8rem 16rem;
      background: rgba(247, 247, 247, 1);
      border-radius: 15rem;
      border: none;
      outline: none;
      font-size: 14rem;
      color: rgba(34, 34, 34, 1);
    }

    span {
      flex-shrink: 0;
      font-size: 16rem;
      color: rgba(102, 102, 102, 1);
    }
  }

  .lsss {
    width: 100%;
    margin: 20rem 0;
    padding: 0 16rem;
    font-size: 15rem;

    .bar {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      p {
        font-size: 16rem;
        color: rgba(51, 51, 51, 1);
      }

      img {
        width: 16rem;
        height: 16rem;
      }
    }

    .content {
      display: flex;
      flex-wrap: wrap;
      margin: 6rem -5rem -6rem -5rem;

      span {
        flex-shrink: 0;
        max-width: calc(100% - 10rem);
        height: 28rem;
        line-height: 28rem;
        padding: 0 12rem;
        margin: 6rem 5rem;
        background: rgba(245, 245, 245, 1);
        border-radius: 14rem;
        font-size: 14rem;
        color: rgba(51, 51, 51, 1);
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow-x: hidden;
      }
    }
  }

  .rmss {
    p {
      margin: 0 16rem;
      font-size: 16rem;
      color: rgba(51, 51, 51, 1);
    }

    .content {
      display: flex;
      flex-wrap: wrap;
      margin: 6rem 0 -6rem 0;
    }
  }

  .dbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20rem 0 30rem 0;

    .text {
      font-size: 14rem;
      color: rgba(253, 83, 83, 1);
    }

    .icon {
      width: 14rem;
      height: 14rem;
      margin-left: 4rem;
    }
  }
}
</style>
