<template>
  <div class="input">
    <div class="topb" style="position: fixed">
      <div>
        <a href="/">
          <div class="no-searchbar-logo-wrapper">
            <img
              src="https://pic.qtfm.cn/qt-msite/qt-logo@2x.png"
              alt="qt-logo"
            />
            <div class="no-searchbar-logo-right">
              <img
                src="https://pic.qtfm.cn/qt-msite/qt-logo-word@2x.png"
                alt="qt-logo-word"
              />
              <div class="no-searchbar-word">
                <span>打开APP</span>
                <span>会员畅听海量内容</span>
              </div>
            </div>
          </div>
        </a>
      </div>
      <a class="btn">打开APP</a>
    </div>
    <div class="search-search-bar">
      <div class="search-bar-wrapper">
        <input
          class="search-bar"
          placeholder="搜索"
          type="text"
          v-model="values"
          @keyup.enter="sp()"
        />
        <img
          class="icon"
          src="https://pic.qtfm.cn/qt-msite/icon-search@2x.png"
          alt="icon"
        />
      </div>
    </div>
    <div class="result">
      <div class="tab">
        <div
          class="item"
          :class="{ active: qb === findIndex }"
          @click="btn(qb), inputs(qb)"
        >
          全部
        </div>
        <div
          class="item"
          :class="{ active: zj === findIndex }"
          @click="btn(zj), inputs(zj)"
        >
          专辑
        </div>
        <div
          class="item"
          :class="{ active: sy === findIndex }"
          @click="btn(sy), inputs(sy)"
        >
          声音
        </div>
        <div
          class="item"
          :class="{ active: zb === findIndex }"
          @click="btn(zb), inputs(zb)"
        >
          主播
        </div>
      </div>
      <!-- <van-loading v-show="loading" size="30px" color="#0094ff" vertical
        >加载中...</van-loading
      >
      <van-loading v-show="loadings" size="30px" color="#0094ff" vertical
        >加载中...</van-loading
      > -->
      <div v-if="inputz?.albumViews?.albums" class="box" v-show="xxx">
        <div
          class="list"
          v-for="albu in inputz.albumViews.albums"
          :key="albu.albumInfo.id"
          @click="
            $router.push({ path: '/play', query: { id: albu.albumInfo.id } })
          "
        >
          <div class="img">
            <img :src="`${albu.albumInfo.cover_path}`" alt="" />
          </div>
          <div class="content">
            <div class="title">
              <h3>{{ albu.albumInfo.title }}</h3>
            </div>
            <div class="desc">
              <span>{{ albu.albumInfo.intro }}</span>
            </div>
            <div class="info">
              <div class="info-item">
                <img src="../assets/icon-play-outline@2x.png" alt="" />
                <span>{{ playCount(albu.albumInfo.play) }}</span>
              </div>
              <div class="info-item">
                <img src="../assets/icon-period@2x.png" alt="" />
                <span>{{ albu.albumInfo.tracks }}</span>
              </div>
              <div class="info-item">
                <img src="../assets/icon-podcaster@2x.png" alt="" />
                <span>{{ albu.albumInfo.nickname }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="inputzs?.albumViews?.albums" class="box" v-show="!xxx">
        <div
          class="list"
          v-for="albu in inputzs.albumViews.albums"
          :key="albu.albumInfo.id"
          @click="
            $router.push({ path: '/play', query: { id: albu.albumInfo.id } })
          "
        >
          <div class="img">
            <img :src="`${albu.albumInfo.cover_path}`" alt="" />
          </div>
          <div class="content">
            <div class="title">
              <h3>{{ albu.albumInfo.title }}</h3>
            </div>
            <div class="desc">
              <span>{{ albu.albumInfo.intro }}</span>
            </div>
            <div class="info">
              <div class="info-item">
                <img src="../assets/icon-play-outline@2x.png" alt="" />
                <span>{{ playCount(albu.albumInfo.play) }}</span>
              </div>
              <div class="info-item">
                <img src="../assets/icon-period@2x.png" alt="" />
                <span>{{ albu.albumInfo.tracks }}</span>
              </div>
              <div class="info-item">
                <img src="../assets/icon-podcaster@2x.png" alt="" />
                <span>{{ albu.albumInfo.nickname }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="inputzs?.trackViews?.tracks" class="box" v-show="!xxx">
        <div
          class="list"
          v-for="albu in inputzs?.trackViews?.tracks"
          :key="albu.trackInfo.id"
        >
          <div class="img">
            <img :src="`${albu.trackInfo.cover_path}`" alt="" />
          </div>
          <div class="content">
            <div class="title">
              <h3>{{ albu.trackInfo.title }}</h3>
            </div>
            <div class="desc">
              <span>{{ albu.trackInfo.intro }}</span>
            </div>
            <div class="info">
              <div class="info-item" v-if="albu.trackInfo.play">
                <img src="../assets/icon-play-outline@2x.png" alt="" />
                <span>{{ playCount(albu.trackInfo.play) }}</span>
              </div>
              <div class="info-item" v-if="albu.trackInfo.tracks">
                <img src="../assets/icon-period@2x.png" alt="" />
                <span>{{ albu.trackInfo.tracks }}</span>
              </div>
              <div class="info-item">
                <img src="../assets/icon-podcaster@2x.png" alt="" />
                <span>{{ albu.trackInfo.nickname }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="inputzs?.userViews?.users" class="box user" v-show="!xxx">
        <div
          class="list"
          v-for="albu in inputzs?.userViews?.users"
          :key="albu.userInfo.id"
          v-show="(inputzs.userViews.users.lenght = 20)"
        >
          <div class="img">
            <img :src="`${albu.userInfo.smallPic}`" alt="" />
          </div>
          <div class="content">
            <div class="title">
              <h3>{{ albu.userInfo.nickname }}</h3>
            </div>
            <div class="info">
              <div class="info-item" v-if="albu.userInfo.albumCount">
                <img src="../assets/icon-play-outline@2x.png" alt="" />
                <span>{{ playCount(albu.userInfo.albumCount) }}</span>
              </div>
              <div class="info-item">
                <img src="../assets/icon-period@2x.png" alt="" />
                <span>{{ albu.userInfo.tracks_counts }}</span>
              </div>
              <div class="info-item">
                <img src="../assets/icon-podcaster@2x.png" alt="" />
                <span>{{ albu.userInfo.followers_counts }}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="zwsj" v-show="inputzs.userViews.users">暂无数据</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      values: "",
      findIndex: "all",
      qb: "all",
      zj: "album",
      sy: "track",
      zb: "user",
      inputzs: [],
      xxx: true,
      loading: false,
    };
  },
  props: {
    inputz: [Object, Array],
    value: String,
    loadings: Boolean,
  },
  created() {
    this.values = this.value;
  },
  methods: {
    playCount: function (playpage) {
      if (playpage > 100000000) {
        return Math.floor(playpage / 10000000) / 10 + "亿";
      } else {
        return Math.floor(playpage / 1000) / 10 + "万";
      }
    },

    btn(index) {
      this.findIndex = index;
      this.xxx = false;
    },
    inputs(djg) {
      this.loading = true;
      this.axios
        .get("https://apis.netstart.cn/ximalaya/search", {
          params: { kw: this.values, core: djg, page: 1, rows: 20 },
        })
        .then((res) => {
          this.inputzs = res.data.data;
          this.loading = false;
        });
    },
    sp() {
      console.log(123);
      this.$emit("input", this.values);
    },
  },
  watch: {},
};
</script>

<style lang="less" scoped>
.input {
  margin-top: 70rem;
  .topb {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 50rem;
    top: 0;
    left: 0;
    padding: 0 16rem;
    z-index: 9999;
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 0rem 6rem 6rem -6rem rgb(0 0 0 / 5%);

    div {
      display: block;
      font-size: 16rem;

      a {
        color: #676b7b;

        .no-searchbar-logo-wrapper {
          display: flex;
          align-items: center;

          img {
            width: 36rem;
            height: 36rem;
            margin-right: 12rem;
          }

          .no-searchbar-logo-right {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            font-size: 12rem;
            color: #d8d8d8;

            img {
              width: 56rem;
              height: 16rem;
              font-size: 12rem;
              color: #d8d8d8;
            }

            .no-searchbar-word {
              span {
                font-size: 12rem;
                color: #d8d8d8;
              }

              span:nth-child(2) {
                color: #c1884b;
                margin-left: 6rem;
              }
            }
          }
        }
      }
    }
  }

  .search-search-bar {
    width: 100%;
    height: 50rem;
    padding: 10rem 16rem;
    margin-top: -40rem;
    margin-left: -15rem;
    position: fixed;
    background-color: white;
    z-index: 999;

    .search-bar-wrapper {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      border-radius: 15rem;

      input {
        width: 100%;
        height: 100%;
        padding: 8rem 8rem 8rem 34rem;
        border: none;
        outline: none;
        background: rgba(247, 247, 247, 1);
        border-radius: 15rem;
        font-size: 14rem;
        color: rgba(34, 34, 34, 1);
      }

      img {
        position: absolute;
        width: 20rem;
        height: 20rem;
        left: 8rem;
      }
    }
  }

  .result {
    width: 100%;
    height: 0;

    .tab {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      height: 40rem;
      padding: 6rem 36rem 16rem 36rem;
      box-shadow: 0rem 1rem 0rem 0rem rgb(239 239 239);

      .item {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 18rem;
        line-height: 18rem;
        font-size: 14rem;
        color: rgba(102, 102, 102, 1);
      }

      .active {
        font-size: 16rem;
        color: rgba(51, 51, 51, 1);
      }

      .active::after {
        content: "";
        position: absolute;
        display: block;
        width: 16rem;
        height: 3rem;
        top: 26rem;
        left: calc((100% - 16rem) / 2);
        background: rgba(253, 83, 83, 1);
        border-radius: 2rem;
      }
    }
  }

  .box {
    width: 350rem;

    .list {
      padding: 5rem 0;
      display: flex;
      align-items: center;
      color: #676b7b;

      .img {
        position: relative;
        width: 64rem;
        height: 64rem;

        img {
          border: 1rem solid rgba(0, 0, 0, 0.05);
          border-radius: 5rem;
          width: 100%;
          height: 100%;
        }
      }

      .content {
        width: 250rem;
        display: flex;
        flex-direction: column;
        margin-left: 15rem;
        padding: 12rem 0;
        box-shadow: 0rem 1rem 0rem 0rem rgb(239 239 239);
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow-x: hidden;

        .title {
          width: 200rem;
          display: flex;
          align-items: center;
          width: 200rem;
          margin-bottom: 6rem;
          font-size: 16rem;
          color: rgba(51, 51, 51, 1);

          h3 {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow-x: hidden;
          }
        }

        .desc {
          width: 100%;
          margin-bottom: 6rem;
          display: flex;
          align-items: center;

          span {
            font-size: 14rem;
            color: rgba(153, 153, 153, 1);
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
        }

        .info {
          width: 100%;
          display: flex;
          align-items: center;

          .info-item {
            display: flex;
            align-items: center;
            flex-shrink: 0;
            margin-right: 12rem;

            img {
              width: 12rem;
              height: 12rem;
              margin-right: 4rem;
            }

            span {
              font-size: 12rem;
              color: rgba(153, 153, 153, 1);
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
              cursor: pointer;
            }
          }
        }
      }
    }
  }

  .user {
    .list {
      display: flex;
      align-items: center;
      padding: 0 16rem;
      color: #676b7b;
      margin-top: 15rem;

      .img {
        position: relative;
        width: 64rem;
        height: 64rem;
        border-radius: 50%;

        img {
          border: 1rem solid rgba(0, 0, 0, 0.05);
          border-radius: 5rem;
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
    }
  }
  .zwsj {
    width: 100%;
    height: 30rem;
    margin-top: 20rem;
    font-size: 20rem;
    text-align: center;
  }
}
</style>
