import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    values: "",
    rankcards: [],
    novelcards: [],
    childcards: [],
    culturecards: [],
    emotioncards: [],
    educationcards: [],
    historycards: [],
    talkcards: [],
    pingbookcards: [],
    operacards: [],
    financecards: [],
    headlinecards: [],
    technologycards: [],
    carcards: [],
    schoolcards: [],
    healthcards: [],
    bestbookcards: [],
    searchhistory: null,

    duration: null,
    currentTime: Number(localStorage.getItem("currentTime")) || 0,
    long: "",

    barback: [],
  },
  getters: {},
  mutations: {
    changeDuration(state, { time }) {
      // console.log(time);
      state.duration = time;
      // console.log(state.duration);
    },
    updateTime(state, { time }) {
      // console.log(time);
      state.currentTime = time;
      // console.log(state.currentTime);
    },
    playing() {
      console.log("播放");
      this.$refs.video.play();
    },
    longs(state) {
      state.long = state.currentTime * (100 / state.duration);
      // console.log(state.long);
    },
    gvales(state, { value }) {
      state.values = value;
    },

    getBack(state, { back }) {
      state.barback = back;
    },
  },

  actions: {},
  modules: {},
});
