<template>
  <li @click="$router.push({ path: '/play', query: { id: card.id } })">
    <img :src="`//imagev2.xmcdn.com/${card.albumInfo.cover}`" alt="" />
    <p>{{ card.albumInfo.title }}</p>
    <div class="info">
      <img src="../../public/imgs/icon-play@2x.png" alt="" />
      <span class="text">{{ playCount(card.statCountInfo.playCount) }}</span>
    </div>
    <div class="badge">{{ index + 1 }}</div>
  </li>
</template>

<script>
export default {
  props: {
    card: Object,
    index: Number,
  },
  methods: {
    playCount: function (card) {
      if (card > 100000000) {
        return Math.floor(card / 10000000) / 10 + "亿";
      } else {
        return Math.floor(card / 1000) / 10 + "万";
      }
    },
  },
};
</script>

<style lang="less" scoped>
li {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  padding: 5rem 0;

  &:nth-child(1) {
    .badge {
      background-image: url("../../public/imgs/icon-badge-1@2x.svg");
    }
  }

  &:nth-child(2) {
    .badge {
      background-image: url("../../public/imgs/icon-badge-2@2x.svg");
    }
  }

  &:nth-child(3) {
    .badge {
      background-image: url("../../public/imgs/icon-badge-3@2x.svg");
    }
  }

  img {
    width: 108rem;
    border-radius: 5rem;
  }

  p {
    height: 36rem;
    padding: 2rem;
    line-height: 18rem;
    font-size: 13rem;
    color: rgba(51, 51, 51, 1);
    overflow: hidden;
  }

  .info {
    width: calc(100% - 2rem);
    height: 16rem;
    padding: 0 6rem;
    color: white;
    position: absolute;
    bottom: 45rem;
    left: 0;
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.3) 100%
    );
    display: flex;
    align-items: center;

    img {
      width: 12rem;
      height: 12rem;
      margin-right: 2rem;
      margin-bottom: 2rem;
    }
    span {
      font-size: 12rem;
      color: rgba(255, 255, 255, 1);
    }
  }

  .badge {
    position: absolute;
    width: 20rem;
    height: 22rem;
    line-height: 22rem;
    left: -1rem;
    top: 5rem;
    background-image: url("../../public/imgs/icon-badge@2x.svg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    border-radius: 5rem 0rem 0rem 0rem;
    font-size: 14rem;
    font-weight: 500;
    color: rgba(255, 255, 255, 1);
    text-align: center;
  }
}
</style>
