<template>
  <div @click="sp">
    <p :class="`a-${index}`">{{ index + 1 }}</p>
    <h3>{{ hse }}</h3>
  </div>
</template>

<script>
export default {
  props: {
    index: Number,
    hse: String,
  },
  methods: {
    sp() {
      this.$emit("input", this.hse);
    },
  },
};
</script>

<style lang="less" scoped>
div {
  display: flex;
  align-items: center;
  width: 50%;
  padding: 0 16rem;
  margin: 6rem 0;

  p {
    width: 16rem;
    height: 16rem;
    line-height: 16rem;
    margin-right: 8rem;
    text-align: center;
    font-size: 14rem;
    color: rgba(102, 102, 102, 1);
  }

  .a-0 {
    color: rgba(225, 52, 48, 1);
  }

  .a-1 {
    color: rgba(255, 102, 65, 1);
  }

  .a-2 {
    color: rgba(255, 161, 57, 1);
  }

  h3 {
    font-size: 14rem;
    color: rgba(102, 102, 102, 1);
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow-x: hidden;
    box-sizing: border-box;
  }
}
</style>
