<template>
  <div id="app">
    <nav><router-link to="/"></router-link></nav>
    <div
      class="playbar"
      @click="bf()"
      v-if="m4a"
      :style="
        this.$store.state.barback?.trackInfo?.cover
          ? `background-image: url(//imagev2.xmcdn.com/${this.$store.state.barback?.trackInfo?.cover});`
          : `background-image: url(//imagev2.xmcdn.com/${pho?.trackInfo?.cover})`
      "
    >
      <van-circle
        v-model="currentRate"
        :rate="long"
        color="red"
        layer-color="#999999"
        :stroke-width="80"
        style="width: 50rem; height: 50rem"
      />
      <img v-show="!playnow" src="../public/imgs/play.png" alt="" />
      <div class="sgright active playing" v-show="playnow">
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>
    <router-view
      @updatatime="$refs.video.currentTime = $event"
      @pushing="push()"
      @playing="play()"
      @songapp="songapp($event)"
    />

    <video
      autoplay
      width="0"
      height="0"
      ref="video"
      @durationchange="changeDuration({ time: $refs.video.duration })"
      @timeupdate="updateTime({ time: $refs.video.currentTime })"
      :src="this.m4a"
    ></video>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  data() {
    return {
      playnow: true,
      m4a: JSON.parse(localStorage.getItem("m4a")),
      pho: JSON.parse(localStorage.getItem("pho")),
      currentRate: 0,
    };
  },

  computed: {
    ...mapState(["playing", "duration", "currentTime", "long", "barback"]),
  },
  methods: {
    ...mapMutations(["changeDuration", "updateTime"]),
    play: function () {
      this.$refs.video.play();
      this.playnow = true;
    },
    push: function () {
      this.$refs.video.pause();
      this.playnow = false;
    },
    bf() {
      this.$router.push({ path: "/playpage" });
    },
    songapp(songappp) {
      this.m4a = songappp.trackInfo.playPath;
    },
  },
  mounted() {
    this.$refs.video.currentTime = this.$store.state.currentTime;

    // this.$refs.video.pause();
  },
  watch: {
    m4a: function (newSong) {
      localStorage.setItem("m4a", JSON.stringify(newSong));
    },

    currentTime(new1) {
      this.currentRate = (new1 * 100) / this.$refs.video.duration;
      localStorage.setItem("currentTime", JSON.stringify(new1));
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes running {
  from {
    transform: rotateX(0deg);
  }

  to {
    transform: rotateX(60deg);
  }
}

nav {
  display: none;
}

video {
  position: fixed;
  z-index: 999;
  top: 100rem;
}

.playbar {
  position: fixed;
  background-size: cover;
  border-radius: 50%;
  bottom: 50rem;
  right: 15rem;
  z-index: 999;
  width: 50rem;
  height: 50rem;

  img {
    display: block;
    width: 15rem;
    height: 15rem;
    position: absolute;
    top: 18rem;
    left: 18rem;
  }

  .sgright {
    width: 25rem;
    height: 25rem;
    margin-right: 15rem;
    background-position: -24rem 0;
    background-size: 166rem 97rem;
    position: absolute;
    top: 12rem;
    left: 12rem;

    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;

      span {
        width: 11%;
        height: 80%;
        border-radius: 10rem;
        background-color: white;
        animation: running 0.6s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;

        &:nth-child(1) {
          animation-delay: 0s;
        }

        &:nth-child(2) {
          animation-delay: -0.2s;
        }

        &:nth-child(3) {
          animation-delay: -0.4s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}

.bf {
  position: absolute;
  top: 0;
  width: 0;
}
</style>
