<template>
  <li @click="$router.push({ path: '/play', query: { id: card.id } })">
    <div class="badge">{{ index + 1 }}</div>
    <img :src="`//imagev2.xmcdn.com/${card?.albumInfo?.cover}`" alt="" />
    <div class="content">
      <h4>{{ card.albumInfo.title.split("|")[0] }}</h4>
      <p>{{ card.albumInfo.customTitle }}</p>
      <div class="info">
        <div class="count">
          <img src="../../public/imgs/icon-play-outline@2x.png" alt="" />
          <span>{{ playCount(card.statCountInfo.playCount) }}</span>
        </div>
        <div class="time">
          <img src="../../public/imgs/icon-period@2x.png" alt="" />
          <span>{{ card.statCountInfo.trackCount }}</span>
        </div>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    card: Object,
    index: Number,
  },
  methods: {
    playCount: function (card) {
      if (card > 100000000) {
        return Math.floor(card / 10000000) / 10 + "亿";
      } else {
        return Math.floor(card / 1000) / 10 + "万";
      }
    },
  },
};
</script>

<style lang="less" scoped>
li {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 12rem 0;

  &:nth-child(1) {
    .badge {
      color: white;
      background-image: url("../../public/imgs/icon-badge-1@2x.svg");
    }
  }

  &:nth-child(2) {
    .badge {
      color: white;
      background-image: url("../../public/imgs/icon-badge-2@2x.svg");
    }
  }

  &:nth-child(3) {
    .badge {
      color: white;
      background-image: url("../../public/imgs/icon-badge-3@2x.svg");
    }
  }

  .badge {
    flex-shrink: 0;
    width: 20rem;
    height: 22rem;
    line-height: 22rem;
    margin-right: 16rem;
    border-radius: 2rem;
    text-align: center;
    font-size: 16rem;
    font-weight: 500;
  }

  img {
    flex-shrink: 0;
    width: 80rem;
    height: 80rem;
    border-radius: 5rem;
  }

  .content {
    display: flex;
    flex-direction: column;
    margin-left: 12rem;
    width: 0;
    flex-grow: 1;

    h4 {
      width: 100%;
      margin-bottom: 6rem;
      font-size: 16rem;
      color: rgba(51, 51, 51, 1);
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }

    p {
      width: 100%;
      margin-bottom: 6rem;
      font-size: 13rem;
      color: rgba(153, 153, 153, 1);
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }

    .info {
      width: 100%;
      display: flex;

      .count {
        display: flex;
        align-items: center;
        margin-right: 12rem;

        img {
          width: 14rem;
          height: 14rem;
          margin-right: 4rem;
        }
        span {
          font-size: 11rem;
          color: rgba(153, 153, 153, 1);
          overflow-x: hidden;
        }
      }

      .time {
        display: flex;
        align-items: center;
        img {
          width: 12rem;
          height: 12rem;
          margin-right: 4rem;
        }
        span {
          font-size: 11rem;
          color: rgba(153, 153, 153, 1);
        }
      }
    }
  }
}
</style>
