<template>
  <ul class="anchor-content">
    <div class="topbar undefined" style="position: fixed">
      <a class="homepage" href="/"
        ><img class="logo" src="https://pic.qtfm.cn/qt-msite/logo@2x.png" /></a
      ><a id="DownloadBtn" class="open-btn" style="cursor: pointer">打开APP</a>
    </div>
    <div class="podcaster-info">
      <div class="header">
        <div class="avatar-wrapper">
          <img
            class="avatar"
            :src="`//imagev2.xmcdn.com/${this.zbxx?.logo}`"
            alt="富顺946"
          />
        </div>
        <h1 class="nickname">{{ this.zbxx.nickname }}</h1>
        <a
          :id="`${this.zbxx.id}`"
          class="follow"
          href="javascript:void(0);"
          style="cursor: pointer"
          >关注</a
        >
      </div>
      <div class="description line-clamp">
        {{ this.zbxx.personalSignature }}
      </div>
    </div>
    <div class="tabs undefined">
      <div
        class="grid-item"
        :class="{ active: !qh }"
        style="width: 50%"
        @click="qh = false"
      >
        最近节目({{ programcard.length }})
      </div>
      <div
        class="grid-item"
        :class="{ active: qh }"
        style="width: 50%"
        @click="qh = true"
      >
        作品列表({{ worklist.length }})
      </div>
    </div>
    <ul class="zj" v-if="qh">
      <WorkList v-for="work in worklist" :key="work.id" :work="work" />
    </ul>
    <ul class="sy" v-else>
      <ProgramCard v-for="card in programcard" :key="card.id" :card="card" />
    </ul>
  </ul>
</template>

<script>
import WorkList from "../components/WorkList.vue";
import ProgramCard from "../components/ProgramCard.vue";
export default {
  data: function () {
    return {
      id: this.$route.query.id,
      zbxx: [],
      worklist: [],
      programcard: [],
      qh: true,
    };
  },
  created() {
    this.axios
      .get(`https://apis.netstart.cn/ximalaya/anchor/queryUserInfo/${this.id}`)
      .then((res) => {
        // console.log(res.data.data.userInfo);
        this.zbxx = res.data.data.userInfo;
      });
    this.axios
      .get(
        `https://apis.netstart.cn/ximalaya/anchor/queryAnchorAlbumsByPage?anchorId=${this.id}&page=1&pageSize=15&asc=false`
      )
      .then((res) => {
        // console.log(res.data.data.albumBriefDetailInfos);
        this.worklist = res.data.data.albumBriefDetailInfos;
      });
    this.axios
      .get(
        `https://apis.netstart.cn/ximalaya/anchor/queryAnchorTracksByPage?anchorId=${this.id}&page=1&pageSize=15&asc=false`
      )
      .then((res) => {
        // console.log(res.data.data.trackDetailInfos);
        this.programcard = res.data.data.trackDetailInfos;
      });
  },
  components: { WorkList, ProgramCard },
};
</script>

<style lang="less" scoped>
@keyframes breath {
  50% {
    transform: scale(1.05);
  }
}
.topbar {
  position: fixed;
  top: 0;
  z-index: 9999;
  width: 100%;
  height: 50rem;
  background-color: #fff;
  box-shadow: 0rem 6rem 6rem -6rem rgb(0 0 0 / 5%);
  .homepage {
    position: absolute;
    top: 0;
    bottom: 0;
    padding-left: 12rem;
    width: 100%;
    background-size: 223rem 30rem;
    background-position: left;
    display: flex;
    align-items: center;
    color: #676b7b;
    background-color: transparent;

    .logo {
      height: 30rem;
      border-style: none;
    }
  }
  .open-btn {
    position: absolute;
    top: 9rem;
    bottom: 9rem;
    text-align: center;
    line-height: 32rem;
    font-size: 12rem;
    color: #fff;
    width: 78rem;
    height: 32rem;
    right: 12rem;
    background: linear-gradient(
      180deg,
      rgba(255, 139, 139, 1) 0%,
      rgba(253, 83, 83, 1) 100%
    );
    box-shadow: 0rem 4rem 20rem -6rem rgb(253 83 83);
    border-radius: 16rem;
    animation: breath 1s ease-in-out infinite;
  }
}
.podcaster-info {
  margin-top: 50rem;
  padding-left: 18rem;
  padding-right: 18rem;
  padding-top: 18rem;
  padding-bottom: 31rem;
  background: #ffffff;
  position: relative;
  .header {
    display: flex;
    align-items: center;
    .avatar-wrapper {
      flex: none;
      width: 60rem;
      height: 60rem;
      border: 1rem solid #979797;
      border-radius: 50%;
      .avatar {
        margin: 3rem;
        width: 54rem;
        height: 54rem;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 50%;
      }
    }
  }
  .nickname {
    font-weight: bold;
    font-size: 20rem;
    color: #333333;
    margin-left: 10rem;
    margin-right: 10rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  .follow {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: none;
    width: 68rem;
    height: 26rem;
    border: 1rem solid #fd5353;
    border-radius: 13rem;
    font-size: 14rem;
    color: #fd5353;
    margin-left: auto;
  }
  .description {
    font-size: 15rem;
    color: #666666;
    padding: 0;
    margin-top: 10rem;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.tabs {
  padding: 0 15rem;
  position: relative;
  width: 100%;
  height: 40rem;
  background-color: #f5f5f8;
  .grid-item {
    float: left;
    text-align: center;
    line-height: 30rem;
    padding: 5rem 0;
    position: relative;
  }
  .active::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 3rem;
    background-color: #d14946;
  }
}
</style>
