<script>
import RankCard from "@/components/RankCard.vue";
import NovelCard from "@/components/NovelCard.vue";

export default {
  name: "HomeView",
  components: { RankCard, NovelCard },
  data() {
    return {
      show: true,
      search: false,
    };
  },

  created() {
    this.axios
      .get(
        "https://apis.netstart.cn/ximalaya/rank/queryRank?clusterCode=hotplay&categoryCode=all"
      )
      .then((res) => {
        this.$store.state.rankcards = res.data.data.rankModuleInfoList;
      });

    this.axios
      .get(
        "https://apis.netstart.cn/ximalaya/rank/queryRank?clusterCode=hotplay&categoryCode=youshengshu"
      )
