<template>
  <div class="play-page">
    <div class="top">
      <div class="logo">
        <img src="../../public/imgs/logo@2x.png" alt="" />
      </div>
      <div class="down">打开APP</div>
    </div>

    <div class="content">
      <div class="cen">
        <div class="pho" v-if="this.pho?.trackInfo?.cover">
          <img
            :src="`//imagev2.xmcdn.com/${this.pho?.trackInfo?.cover}`"
            alt=""
          />
          <div class="bar">
            <img
              v-if="!showplay"
              v-show="m4a"
              @click="
                $emit('pushing');
                showplay = true;
              "
              src="../../public/imgs/pause-lg.png"
              alt=""
            />
            <img
              v-else
              @click="
                $emit('playing');
                showplay = false;
              "
              src="../../public/imgs/play-lg.png"
              alt=""
            />
            <img
              v-show="!m4a"
              @click="
                $emit('playing');
                showplay = false;
              "
              src="../../public/imgs/play-lg.png"
              alt=""
            />
          </div>
        </div>
        <h1>
          {{ pho.trackInfo.title }}
        </h1>
      </div>
    </div>

    <div class="btns">
      <span>倍速播放</span>
      <span>下载节目</span>
    </div>

    <div class="playbar">
      <span>{{ secTotime(currentTime) }}</span>
      <input
        type="range"
        min="0"
        :max="duration"
        :value="currentTime"
        @input="draging = true"
        @change="
          draging = false;
          $emit('updatatime', $event.target.value);
        "
      />
      <span>{{ secTotime(duration) }}</span>
    </div>

    <div class="btn" v-if="m4a"><span>听完整版</span></div>
    <div class="btn" v-else><span>VIP</span></div>

    <div class="recommend">
      <div class="info">
        <div class="pho" v-if="playanchor?.userInfo?.logo">
          <img
            :src="`//imagev2.xmcdn.com/${playcontent?.albumDetailInfo?.albumInfo?.cover}`"
            alt=""
          />
        </div>
        <div class="text">
          <h4>{{ playcontent?.albumDetailInfo?.albumInfo?.customTitle }}</h4>
          <div class="icons">
            <div class="playcount">
              <img src="../../public/imgs/play-fa.png" alt="" />
              <p>
                {{
                  playCount(
                    playcontent?.albumDetailInfo?.statCountInfo?.subscribeCount
                  )
                }}
              </p>
            </div>
            <div class="time">
              <img src="../../public/imgs/count-fa.png" alt="" />
              <p>
                {{ playcontent?.albumDetailInfo?.statCountInfo?.trackCount }} 期
              </p>
            </div>
          </div>
        </div>
        <span>订阅</span>
      </div>
    </div>

    <div class="list">
      <ul>
        <li
          v-for="play in playlist"
          :key="play.id"
          @click="getPlay(play), playapp(play)"
        >
          <div class="cont">
            <img src="../../public/imgs/play-circle.png" alt="" />
            <div class="text">
              <h2>{{ play.trackInfo.title }}</h2>
              <div class="item">
                <div class="time">
                  <img src="../../public/imgs/time-fa.png" alt="" />
                  <span>{{ getTime(play.trackInfo.duration) }}</span>
                </div>
                <div class="playcount">
                  <img src="../../public/imgs/play-fa.png" alt="" />
                  <span>{{ playCount(play?.statCountInfo?.playCount) }}</span>
                </div>
                <div class="date">
                  <img src="../../public/imgs/update-fa.png" alt="" />
                  <span>{{ getDate(play?.trackInfo?.createdTime) }}</span>
                </div>
              </div>
            </div>
          </div>
          <img src="../../public/imgs/download-btn.png" alt="" />
        </li>
      </ul>
      <p>打开蜻蜓 倾听更多</p>
    </div>

    <div class="anchor">
      <h3>主播信息</h3>
      <div
        class="info"
        @click="
          $router.push({ path: '/anchorcontent', query: { id: playanchor.id } })
        "
      >
        <div class="pho" v-if="playanchor?.userInfo?.logo">
          <img
            :src="`//imagev2.xmcdn.com/${playanchor?.userInfo?.logo}`"
            alt=""
          />
        </div>
        <div class="text">
          <h4>{{ playanchor?.userInfo?.personDescribe }}</h4>
          <p>{{ playanchor?.userInfo?.personalSignature }}</p>
        </div>
        <span>关注</span>
      </div>
      <ul class="list">
        <li v-for="man in anchor" :key="man.id" @click="getBack({ back: man })">
          <div class="pho">
            <img :src="`//imagev2.xmcdn.com/${man?.albumInfo?.cover}`" alt="" />
            <div class="playcount">
              <img src="../../public/imgs/icon-play@2x.png" alt="" />
              <span>{{ playCount(man?.statCountInfo?.playCount) }}</span>
            </div>
          </div>
          <div class="text">
            <p>{{ man?.albumInfo?.title }}</p>
          </div>
        </li>
      </ul>
      <div class="look">
        <span>APP内查看主播</span>
        <img src="../../public/imgs/arrow-down.png" alt="" />
      </div>
    </div>

    <div class="listen-more">
      <h3>大家都在听</h3>
      <ul>
        <li
          v-for="listen in listenmore"
          :key="listen.id"
          @click="$router.push({ path: '/play', query: { id: listen.id } })"
        >
          <div class="pho" v-if="playanchor?.userInfo?.logo">
            <img
              :src="`//imagev2.xmcdn.com/${listen?.albumInfo?.cover}`"
              alt=""
            />
          </div>
          <div class="text">
            <h4>{{ listen?.albumInfo?.title }}</h4>
            <p>{{ listen?.albumInfo?.shortIntro }}</p>
            <div class="icons">
              <div class="playcount">
                <img src="../../public/imgs/play-fa.png" alt="" />
                <p>
                  {{ playCount(listen?.statCountInfo?.playCount) }}
                </p>
              </div>
              <div class="time">
                <img src="../../public/imgs/count-fa.png" alt="" />
                <p>
                  {{ listen?.statCountInfo?.trackCount }}
                  期
                </p>
              </div>
            </div>
          </div>
          <span>收藏</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";

export default {
  data() {
    return {
      playcontent: null,
      playanchor: null,
      anchor: null,
      playlist: null,
      playpage: null,
      pho: JSON.parse(localStorage.getItem("pho")),

      pageid: this.$route.query.id
        ? this.$route.query.id
        : JSON.parse(localStorage.getItem("pageid")),

      showplay: false,
      value: 0,
      draging: false,

      m4a: JSON.parse(localStorage.getItem("m4a")),

      listenmore: null,
    };
  },

  computed: {
    ...mapState(["duration", "currentTime", "barback"]),
  },

  created() {
    // console.log(this.pageid);
    localStorage.setItem("pageid", this.pageid);

    this.axios
      .get(
        `https://apis.netstart.cn/ximalaya/album/queryAlbumPage/${JSON.parse(
          localStorage.getItem("playid")
        )}`
      )
      .then((res) => {
        // console.log(res.data.data);
        this.playcontent = res.data.data;

        this.axios
          .get(
            `https://apis.netstart.cn/ximalaya/anchor/queryUserInfo/${this.playcontent.anchorId}`
          )
          .then((res) => {
            // console.log(res.data.data);
            this.playanchor = res.data.data;
          });

        this.axios
          .get(
            `https://apis.netstart.cn/ximalaya/anchor/queryAnchorPage/${this.playcontent.anchorId}`,
            {
              params: { pageSize: 20, tabType: 0 },
            }
          )
          .then((res) => {
            // console.log(res.data.data);
            this.anchor =
              res.data.data.anchorAlbumPageRecords.albumBriefDetailInfos.splice(
                0,
                3
              );
          });

        this.axios
          .get(
            "https://apis.netstart.cn/ximalaya/album/queryAlbumRelatedRecommendAlbums",
            {
              params: {
                page: 1,
                albumId: this.playcontent.anchorId,
                pageSize: 10,
              },
            }
          )
          .then((res) => {
            // console.log(res.data.data.albumBriefDetailInfos);
            this.listenmore = res.data.data.albumBriefDetailInfos;
          });
      });

    this.axios
      .get(
        "https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage",
        {
          params: {
            albumId: JSON.parse(localStorage.getItem("playid")),
            page: 1,
            pageSize: 10,
            asc: true,
          },
        }
      )
      .then((res) => {
        // console.log(res.data.data.trackDetailInfos);
        this.playlist = res.data.data.trackDetailInfos;
      });

    this.axios
      .get(
        `https://apis.netstart.cn/ximalaya/track/queryTrackPage/${this.pageid}`
      )
      .then((res) => {
        // console.log(res.data.data);
        this.playpage = res.data.data;
      });
  },
  methods: {
    playCount: function (playpage) {
      if (playpage > 100000000) {
        return Math.floor(playpage / 10000000) / 10 + "亿";
      } else {
        return Math.floor(playpage / 1000) / 10 + "万";
      }
    },

    getTime: function (s) {
      var t = "";
      if (s > -1) {
        var min = Math.floor(s / 60) % 60;
        var sec = s % 60;
        if (min < 10) {
          t += "0";
        }
        t += min + ":";
        if (sec < 10) {
          t += "0";
        }
        t += sec.toFixed(0);
      }
      return t;
    },

    getDate: function (time) {
      let date = new Date(time);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      return `${year}-${month}-${day}`;
    },

    playapp(song) {
      this.$emit("songapp", song);
    },

    secTotime(s) {
      // console.log(s);
      var t = "";
      if (s > -1) {
        var min = Math.floor(s / 60) % 60;
        var sec = s % 60;
        if (min < 10) {
          t += "0";
        }
        t += min + ":";
        if (sec < 10) {
          t += "0";
        }
        t += sec.toFixed(0);
      }
      return t;
    },

    getPlay: function (play) {
      localStorage.setItem("pho", JSON.stringify(play));
      this.pho = JSON.parse(localStorage.getItem("pho"));
    },

    ...mapMutations(["getBack"]),
  },
};
</script>

