<template>
  <div class="play-view">
    <div class="top">
      <div class="logo">
        <img src="../../public/imgs/logo@2x.png" alt="" />
      </div>
      <div class="down">打开APP</div>
    </div>

    <div class="content">
      <div class="cen">
        <div class="pho" v-if="playcontent?.albumDetailInfo?.albumInfo.cover">
          <img
            :src="`//imagev2.xmcdn.com/${playcontent?.albumDetailInfo?.albumInfo?.cover}`"
            alt=""
          />
          <div class="info">
            <img src="../../public/imgs/play-count-fa.png" alt="" />
            <span>{{
              playCount(playcontent?.albumDetailInfo?.statCountInfo.playCount)
            }}</span>
          </div>
        </div>
        <h1>{{ playcontent?.albumDetailInfo?.albumInfo.title }}</h1>
        <p>主播 : {{ playanchor?.userInfo?.nickname }}</p>
      </div>
    </div>

    <div class="intro">
      <h3>简介</h3>
      <p>{{ playcontent?.albumDetailInfo?.albumInfo.shortIntro }}</p>
    </div>

    <div class="btn"><span>听完整版</span></div>

    <div class="play-title">
      <div class="title">
        节目({{ playcontent?.albumDetailInfo?.statCountInfo?.trackCount }})
      </div>
      <span class="down">批量下载</span>
    </div>

    <div class="list">
      <ul>
        <li
          v-for="play in playlist"
          :key="play.id"
          @click="
            $router.push({
              path: '/playpage',
              query: { id: play.id, pho: play },
            });
            getPlay(play);
            song(play);
            getBack({ back: play });
          "
        >
          <div class="cont">
            <img src="../../public/imgs/play-circle.png" alt="" />
            <div class="text">
              <h2>{{ play.trackInfo.title }}</h2>
              <div class="item">
                <div class="time">
                  <img src="../../public/imgs/time-fa.png" alt="" />
                  <span>{{ getTime(play.trackInfo.duration) }}</span>
                </div>
                <div class="playcount">
                  <img src="../../public/imgs/play-fa.png" alt="" />
                  <span>{{ playCount(play?.statCountInfo?.playCount) }}</span>
                </div>
                <div class="date">
                  <img src="../../public/imgs/update-fa.png" alt="" />
                  <span>{{ getDate(play?.trackInfo?.createdTime) }}</span>
                </div>
              </div>
            </div>
          </div>
          <img src="../../public/imgs/download-btn.png" alt="" />
        </li>
      </ul>
      <p>加载更多</p>
    </div>

    <!-- <div class="anchor">
      <h3>主播信息</h3>
      <div class="info">
        <div class="pho" v-if="playanchor?.userInfo?.logo">
          <img
            :src="`//imagev2.xmcdn.com/${playanchor?.userInfo?.logo}`"
            alt=""
          />
        </div>
        <div class="text">
          <h4>{{ playanchor?.userInfo?.personDescribe }}</h4>
          <p>{{ playanchor?.userInfo?.personalSignature }}</p>
        </div>
        <span>关注</span>
      </div>
      <ul class="list">
        <li v-for="man in anchor" :key="man.id">
          <div class="pho">
            <img :src="`//imagev2.xmcdn.com/${man?.albumInfo?.cover}`" alt="" />
            <div class="playcount">
              <img src="../../public/imgs/icon-play@2x.png" alt="" />
              <span>{{ playCount(man?.statCountInfo?.playCount) }}</span>
            </div>
          </div>
          <div class="text">
            <p>{{ man?.albumInfo?.title }}</p>
          </div>
        </li>
      </ul>
      <div class="look">
        <span>APP内查看主播</span>
        <img src="../../public/imgs/arrow-down.png" alt="" />
      </div>
    </div> -->
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  data() {
    return {
      playcontent: null,
      playanchor: null,
      anchor: null,
      playlist: null,
      playid: this.$route.query.id
        ? this.$route.query.id
        : JSON.parse(localStorage.getItem("playid")),
      // pho: this.play ? this.play : JSON.parse(localStorage.getItem("pho")),
    };
  },

  computed: {
    ...mapState(["barback"]),
  },

  created() {
    localStorage.setItem("playid", this.playid);
    // localStorage.setItem("pho", JSON.stringify(this.pho));

    this.axios
      .get(
        `https://apis.netstart.cn/ximalaya/album/queryAlbumPage/${this.playid}`
      )
      .then((res) => {
        // console.log(res.data.data);
        this.playcontent = res.data.data;

        this.axios
          .get(
            `https://apis.netstart.cn/ximalaya/anchor/queryUserInfo/${this.playcontent.anchorId}`
          )
          .then((res) => {
            // console.log(res.data.data);
            this.playanchor = res.data.data;
          });

        this.axios
          .get(
            `https://apis.netstart.cn/ximalaya/anchor/queryAnchorPage/${this.playcontent.anchorId}`,
            {
              params: { pageSize: 20, tabType: 0 },
            }
          )
          .then((res) => {
            // console.log(res.data.data);
            this.anchor =
              res.data.data.anchorAlbumPageRecords.albumBriefDetailInfos.splice(
                0,
                3
              );
          });
      });

    this.axios
      .get(
        "https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage",
        {
          params: {
            albumId: this.playid,
            page: 1,
            pageSize: 10,
            asc: true,
          },
        }
      )
      .then((res) => {
        // console.log(res.data.data.trackDetailInfos);
        this.playlist = res.data.data.trackDetailInfos;
      });
  },
  methods: {
    playCount: function (playpage) {
      if (playpage > 100000000) {
        return Math.floor(playpage / 10000000) / 10 + "亿";
      } else {
        return Math.floor(playpage / 1000) / 10 + "万";
      }
    },

    getTime: function (s) {
      var t = "";
      if (s > -1) {
        var min = Math.floor(s / 60) % 60;
        var sec = s % 60;
        if (min < 10) {
          t += "0";
        }
        t += min + ":";
        if (sec < 10) {
          t += "0";
        }
        t += sec.toFixed(0);
      }
      return t;
    },

    getDate: function (time) {
      let date = new Date(time);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      return `${year}-${month}-${day}`;
    },

    getPlay: function (play) {
      localStorage.setItem("pho", JSON.stringify(play));
    },
    song(play) {
      this.$emit("songapp", play);
    },

    ...mapMutations(["getBack"]),
  },
};
</script>

