<template>
  <li class="item">
    <div class="info">
      <h3>{{ card.trackInfo.title }}</h3>
      <p>
        {{ getDate(card.trackInfo.updatedTime) }}&ensp;&ensp;{{
          card.trackInfo.createdAt
        }}
      </p>
    </div>
    <div class="play-btn"></div>
  </li>
</template>

<script>
export default {
  data: function () {
    return {};
  },
  methods: {
    getDate: function (time) {
      let date = new Date(time);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      return `${year}-${month}-${day}`;
    },
  },
  props: {
    card: Object,
  },
};
</script>

<style lang="less" scoped>
.item {
  background-color: transparent;
  color: #676b7b;
  display: flex;
  align-items: center;
  border-bottom: 1rem solid #dcdee3;
  height: auto;
  padding: 15rem;

  .info {
    flex: 1;
    overflow: hidden;
    margin-left: 5rem;
    h3 {
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 16rem;
      color: #3d3d3d;
    }
    p {
      font-size: 14rem;
      color: #676b7b;
    }
  }
  .play-btn {
    flex: none;
    width: 34rem;
    height: 34rem;
    background-image: url("../../public/imgs/list_play.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-left: 5rem;
    margin-right: 15rem;
  }
}
</style>
